.editor-wrapper {
	display: flex;
	.editor {
		background-color: $editor_background-color;
		border-color: $editor_border-color;
		border-bottom-color: $editor_border-bottom-color;
		color: $editor_color;
		border-radius: 3px;
		margin: 10px;
		font-family: Arial, Helvetica, sans-serif;
		flex: 1;
		border-style: solid;
		border-width: 1px;
		box-sizing: border-box;
		.react-tabs {
			-webkit-tap-highlight-color: transparent;
			.editor-textarea,
			.editor-preview {
				margin: 10px;
				margin-bottom: 0;
            }
            .editor-textarea {
                textarea {
                    background-color: $editor-textarea_background-color;
                    border: $editor-textarea_border-color 1px solid;
                    border-radius: 2px;
                    box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
                    box-sizing: border-box;
                    color: $editor-textarea_color;
                    font-size: 14px;
                    line-height: 1.6;
                    min-height: 125px;
                    outline: none;
                    padding: 10px;
                    resize: vertical;
                    width: 100%;
                    &:disabled {
                        cursor: no-drop;
                    }
                }
            }
            .editor-preview {
                .markdown-render {
                    padding: 10px;
                    width: 100%;
                    -webkit-box-sizing: border-box;
                    -moz-box-sizing: border-box;
                    box-sizing: border-box;
                    background-color: $markdown-render_background-color;
                    border: $markdown-render_border-color 1px solid;
                    color: $markdown-render_color;
                    border-radius: 2px;
                    text-rendering: auto;
                    letter-spacing: normal;
                    word-spacing: normal;
                    text-transform: none;
                    text-indent: 0px;
                    text-shadow: none;
                    text-align: start;
                    -webkit-rtl-ordering: logical;
                    flex-direction: column;
                    white-space: pre-wrap;
                    overflow-wrap: break-word;
                    margin: 0;
                    line-height: 1.6;
                    font-size: 14px;
                    background-repeat: no-repeat;
                    background-position: right 8px center;
                    outline: none;
                    box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
                    box-sizing: border-box;
                    min-height: 125px;
                }
            }
			&__tab-list {
				border-bottom: $react-tabs-tab-list_border-bottom-color 1px
					solid;
				color: $react-tabs-tab-list_color;
				padding: 10px;
				padding-bottom: 0;
				background-color: $react-tabs-tab-list_background-color;
				margin-top: 0;
			}
			&__tab {
				display: inline-block;
				border: 1px solid transparent;
				border-bottom: none;
				bottom: -1px;
				position: relative;
				list-style: none;
				padding: 6px 12px;
				cursor: pointer;
				align-items: flex-start;
				box-sizing: border-box;
				color: $react-tabs-tab-list-tab_color;
				cursor: pointer;
				display: inline-block;
				flex-basis: auto;
				flex-grow: 1;
				flex-shrink: 1;
				font-size: 14px;
				font-weight: 400;
				height: 37px;
				letter-spacing: normal;
				line-height: 20px;
				padding-bottom: 8px;
				padding-left: 16px;
				padding-right: 16px;
				padding-top: 8px;
				text-align: center;
				text-decoration-color: $react-tabs-tab-list-tab_color;
				text-decoration-line: none;
				text-decoration-style: solid;
				&--selected {
					background-color: $react-tabs-tab-list-tab-selected_background-color;
					border-color: $react-tabs-tab-list-tab-selected_border-color;
					color: $react-tabs-tab-list-tab-selected_color;
					border-radius: 5px 5px 0 0;
				}
				&--disabled {
					cursor: no-drop;
				}
				&:focus {
					outline: 0;
				}
			}
			&__tab-panel {
				display: none;
				&--selected {
					display: block;
				}
			}
		}
		.editor-buttons {
			box-sizing: border-box;
			display: flex !important;
			justify-content: flex-end !important;
			margin: 10px;
			.btn {
				position: relative;
				display: inline-block;
				padding: 6px 12px;
				font-size: 14px;
				font-weight: 600;
				line-height: 20px;
				white-space: nowrap;
				vertical-align: middle;
				cursor: pointer;
				user-select: none;
				background-repeat: repeat-x;
				background-position: -1px -1px;
				background-size: 110% 110%;
				border: 1px solid rgba(27, 31, 35, 0.2);
				border-radius: 0.25em;
				-webkit-appearance: none;
			}
			.btn-primary {
				color: #fff;
				background-color: #28a745;
				background-image: linear-gradient(
					-180deg,
					#34d058,
					#28a745 90%
				);
				text-decoration: none;
				outline: none;
				&:hover {
					background-color: #269f42;
					background-image: linear-gradient(
						-180deg,
						#2fcb53,
						#269f42 90%
					);
					background-position: -0.5em;
					border-color: rgba(27, 31, 35, 0.5);
				}
			}
		}
	}
	.arrow-box {
		position: relative;
		&:after,
		&:before {
			right: 100%;
			top: 20px;
			border: solid transparent;
			content: ' ';
			height: 0;
			width: 0;
			position: absolute;
			pointer-events: none;
		}
		&:after {
			border-color: transparent;
			border-right-color: $comment-header_background-color;
			border-width: 5px;
			margin-top: -5px;
		}
		&:before {
			border-color: transparent;
			border-right-color: $comment_border-color;
			border-width: 6px;
			margin-top: -6px;
		}
	}
}
